<template>
  <div>
    <h3>Тип транспорта</h3>
    <select v-model="changeTransport">
      <option value="">Все типы транспорта</option>
      <option v-for="type in uniqueTransportType" :key="type" :value="type">{{ type }}</option>
    </select>
  </div>

</template>


<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'TransportFilter',
  computed: {
    ...mapGetters(['uniqueTransportType', 'getCurrentTransportType']),
    changeTransport: {
      get() {
        return this.getCurrentTransportType;
      },
      set(val) {
        this.setTransportType(val)
      }
    }
  },
  methods: {
    ...mapActions(['setTransportType'])
  }
}

</script>