<template>
  <div>
    <h3>Рік випуску</h3>
    <div class="year-car">
      <select v-model="changeYearFrom">
        <option v-for="year in allYear" :key="year" :value="year">{{ year }}</option>
      </select>
      <select v-model="changeYearTo">
        <option v-for="year in allYear" :key="year" :value="year">{{ year }}</option>
      </select>
    </div>
  </div>
</template>
<script>

import {mapActions, mapGetters} from "vuex";

export default {
  name: 'YearsFilter',
  computed: {
    ...mapGetters(["allYear", "getCurrentYearFrom", "getCurrentYearTo"]),
    changeYearFrom: {
      get() {
        return this.getCurrentYearFrom
      },
      set(val) {
        this.setYearFrom(val)
      }
    },
    changeYearTo: {
      get() {
        return this.getCurrentYearTo
      },
      set(val) {
        this.setYearTo(val)
      }
    },
  },
  methods: {
    ...mapActions(['setYearFrom', 'setYearTo'])
  }
}


</script>

<style>
.year-car {
  display: flex;
  gap: 20px;
}
</style>