<template>
  <div>
    <h3>Модель</h3>

    <select v-model="changeModel">
      <option v-for="type in uniqueModels" :key="type" :value="type"> {{ type }}</option>
    </select>
  </div>

</template>


<script>
import {mapGetters, mapActions} from "vuex";
export default {
  name: 'ModelFilter',
  computed: {
    ...mapGetters(['uniqueModels', 'getCurrentModel']),
    changeModel: {
      get() {
        return this.getCurrentModel
      },
      set(val){
        return this.setModel(val)
      }
    }
  },
  methods: {
    ...mapActions(['setModel'])
  }
}

</script>