<template>
  <div>
    <h3>Марка</h3>
    <div>
      <select v-model="changeBrand">
        <option v-for="type in uniqueBrands" :key="type" :value="type">
          {{ type }}
        </option>
      </select>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'BrandFilter',
  computed: {
    ...mapGetters(['uniqueBrands']),
    changeBrand: {
      get() {
        return this.getCurrentBrand;
      },
      set(val) {
        this.setBrand(val)
      }
    }
  },
  methods: {
    ...mapActions(['setBrand'])
  }
}


</script>