import {createStore} from 'vuex';
import {cars} from '../constants/carsData';
import {filterCars, getUniqueValues} from '../store/helper/filter';

export default createStore({
    state() {
        return {
            cars: cars,
            selectedBrand: null,
            selectedModel: null,
            selectedBody: [],
            selectedTransportType: null,
            selectedYearFrom: null,
            selectedYearTo: null,
            filteredCars: [],
        };
    },
    getters: {
        uniqueBrands: (state) => getUniqueValues(state.cars, 'brand'),
        uniqueModels: (state) => {
            if (!state.selectedBrand) return getUniqueValues(state.cars, 'model');
            const filteredCars = state.cars.filter(car => car.brand === state.selectedBrand);
            return getUniqueValues(filteredCars, 'model');
        },
        uniqueBody: (state) => getUniqueValues(state.cars, 'bodyType'),
        uniqueTransportType: (state) => getUniqueValues(state.cars, 'transportType'),
        allYear: (state) => getUniqueValues(state.cars, 'year'),

        filteredCars: (state) => {
            return filterCars(
                state.cars,
                state.selectedBrand,
                state.selectedModel,
                state.selectedBody,
                state.selectedTransportType,
                state.selectedYearFrom,
                state.selectedYearTo
            );
        },
        getCurrentBrand: ({selectedBrand}) => selectedBrand,
        getCurrentBody: ({selectedBody}) => selectedBody,
        getCurrentModel: ({selectedModel}) => selectedModel,
        getCurrentYearFrom: ({selectedYearFrom}) => selectedYearFrom,
        getCurrentYearTo: ({selectedYearTo}) => selectedYearTo,
        getCurrentTransportType: ({selectedTransportType}) => selectedTransportType,
    },
    mutations: {
        setSelectedBrand(state, brand) {
            state.selectedBrand = brand;
        },
        setSelectedModel(state, model) {
            state.selectedModel = model;
        },
        setSelectedBody(state, bodyTypes) {
            state.selectedBody = bodyTypes;
        },
        setSelectedTransportTYpe(state, transportType) {
            state.selectedTransportType = transportType
        },
        setSelectedYearFrom(state, year){
            console.log("Mutation: setSelectedYearFrom", year);
            state.selectedYearFrom = year
        },
        setSelectedYearTo(state, year){
            state.selectedYearTo = year
        },
        setFilteredCars(state, cars) {
            state.filteredCars = cars;
        },
    },
    actions: {
        loadCars({commit}) {
            commit('setFilteredCars', cars);
        },
        changeBrand({commit, state}, brand) {
            commit('setSelectedBrand', brand);
            commit('setFilteredCars', filterCars(state.cars, brand, state.selectedModel));
        },
        changeModel({commit, state}, model) {
            commit('setSelectedModel', model);
            commit('setFilteredCars', filterCars(state.cars, state.selectedBrand, model));
        },
        changeBody({commit, state}, bodyType){
            commit('setSelectedBody', filterCars(state.cars, bodyType, state.selectedBody));
        },
        setBrand({commit}, brand) {
            commit('setSelectedBrand', brand)
        },
        setBody({ commit }, bodyTypes) {
            commit("setSelectedBody", bodyTypes);
        },
        setModel({commit}, model) {
            commit('setSelectedModel', model)
        },
        setYearFrom({commit}, year){
          commit('setSelectedYearFrom', year)
        },
        setYearTo({commit}, year){
            commit('setSelectedYearTo', year)
        },
        setTransportType({commit}, transportType) {
            commit('setSelectedTransportTYpe', transportType)
        },
    },
});