<template>
  <div>
    <h3>Тип кузова</h3>
    <div class="type-car">
      <label>
        <input
            type="checkbox"
            :checked="changeBody.length === 0"
            @change="toggleAll"
        />
        Все типы кузова
      </label>
      <label v-for="type in uniqueBody" :key="type">
        <input
            type="checkbox"
            :value="type"
            :checked="changeBody.includes(type)"
            @change="toggleSelection(type)"
        />
        {{ type }}
      </label>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "BodyFilter",
  computed: {
    ...mapGetters(["uniqueBody", "getCurrentBody"]),

    changeBody: {
      get() {
        return this.getCurrentBody;
      },
      set(val) {
        this.setBody(val);
      }
    }
  },
  methods: {
    ...mapActions(["setBody"]),
    toggleAll(event) {
      if (event.target.checked) {
        this.setBody([]);
      }
    },
    toggleSelection(type) {
      let updatedSelection = [...this.changeBody];
      if (updatedSelection.includes(type)) {
        updatedSelection = updatedSelection.filter((t) => t !== type);
      } else {
        updatedSelection.push(type);
      }
      this.setBody(updatedSelection);
    }
  }
};
</script>

<style>
.type-car label{
  display: block;
}
</style>
