export const filterCars = (
    cars,
    selectedBrand,
    selectedModel,
    selectedBody,
    selectedTransportType,
    selectedYearFrom,
    selectedYearTo
) => {
    let filteredCars = cars;

    if (selectedBrand) {
        filteredCars = filteredCars.filter(car => car.brand === selectedBrand);
    }

    if (selectedModel && selectedModel !== null) {
        filteredCars = filteredCars.filter(car => car.model === selectedModel);
    }
    if (selectedTransportType) {
        filteredCars = filteredCars.filter(car => car.transportType === selectedTransportType)
    }
    if (selectedBody.length > 0) {
        filteredCars = filteredCars.filter(car => selectedBody.includes(car.bodyType));
    }

    if (selectedYearFrom) {
        filteredCars = filteredCars.filter(car => car.year >= selectedYearFrom);
    }
    if (selectedYearTo) {
        filteredCars = filteredCars.filter(car => car.year <= selectedYearTo);
    }
    return filteredCars;
};


export const getUniqueValues = (array, key) => {
    return [...new Set(array.map(item => item[key]))];
};